body {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
}

p {
  color: #555;
}
section {
  padding: 60px 0;
  position: relative;
}

.text-shadow {
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
}
.text-300 {
  font-weight: 300 !important;
}
.text-400 {
  font-weight: 400 !important;
}
.text-500 {
  font-weight: 500 !important;
}
.title {
  display: inline-block;
  font-size: 2.9rem;
  font-weight: 100;
  padding: 10px 0;
  margin-bottom: 44px;
  text-align: center;
  border-bottom: solid 1px #ccc;
  border-top: solid 1px #ccc;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  line-height: 1.2;
  color: #555;
  margin-top: 20px;
  letter-spacing: 0;
}
.title span {
  font-size: 2.5rem;
}

.bg-gradient-pink {
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#4fbfa8),
    to(#74cdba)
  ) !important;
  background: linear-gradient(to bottom, #f1c2e8, #fdf9fd) !important;
}
.bg-gradient * {
  color: inherit;
}

#filter {
  text-align: center;
  margin: 20px 0;
  padding: 0;
}
#filter li {
  display: inline-block;
  list-style-type: none;
  font-family: "Roboto Slab", serif;
  margin-bottom: 30px;
  border-top: solid 1px #4fbfa8;
  border-bottom: solid 1px #4fbfa8;
  margin: 0 10px 20px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
#filter li div {
  display: inline-block;
  padding: 10px;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
#filter li.active,
#filter li:hover {
  border: none;
}
#filter li.active div,
#filter li:hover div {
  color: #fff;
  text-decoration: none;
}
.mywork {
  margin-bottom: 30px;
  overflow: hidden;
}
@media (max-width: 767.98px) {
  .mywork {
    max-width: 350px;
    margin: 0 auto;
    margin-bottom: 30px;
  }
}
.mywork a {
  width: 100%;
  height: 100%;
  position: relative;
  display: block;
}
.mywork h3 {
  font-weight: 400;
}
.mywork img {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.mywork .overlay {
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  opacity: 0;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  padding: 15px;
}
.mywork .overlay p {
  color: #fff;
}
.mywork .mywork-title {
  text-transform: uppercase;
  letter-spacing: 0.1em;
}
.mywork:hover img {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
.mywork:hover .overlay {
  opacity: 1;
  -webkit-transform: none;
  transform: none;
}
#detail {
  display: none;
}
#detail .btn {
  margin: 10px;
}
#detail .close {
  position: absolute;
  right: 0;
  z-index: 999;
  display: block;
  width: 50px;
  height: 50px;
  margin-bottom: 20px;
  font-size: 3rem;
  opacity: 0.3;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
#detail .close:hover {
  opacity: 1;
}
#detail-slider {
  margin-top: 50px;
}

nav.navbar {
  padding-top: 0;
  padding-bottom: 0;
  background: #fff;
  border-bottom: 1px solid #e6e6e6;
}
@media (max-width: 991.98px) {
