/*fullscren menu*/
.dropdown {
  position: relative;
  display: inline-block;
  float: left !important;
  cursor: pointer;
}

.dropdown-content {
  padding-top: 10px;
  padding-bottom: 10px;
  display: none;
  left: -25px; /*position menu content with the start pos of services*/
  position: absolute;
  border: 1px solid #ddd;
  border-radius: 2.5%;
  text-align: left;
  background-color: #ffffff;
  min-width: 160px;
  /*box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);*/
  z-index: 1000;
}

.dropdown-content a {
  padding: 3px 24px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: #f8f9fa;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.nav-divider {
  border-bottom: 1px solid #f0f0f0;
}

.caret {
  position: relative;
  cursor: pointer;
}

.caret:after {
  content: "";
  position: absolute;
  top: 25%;
  font-size: inherit;
  margin-left: -9px;
  border-top: 5px solid;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
}

/*mobile menu*/
.dropdown-menu {
  border: none !important; /*border: 1px solid rgba(0,0,0,.15);*/
  font-size: 0.875rem !important;
}

.dropdown-item {
  text-align: center !important;
  color: rgba(0, 0, 0, 0.5) !important;
  text-transform: none;
  font-size: 17px; 
  letter-spacing: 1px;
}

.dropdown-item:focus,
.dropdown-item:hover {
  color: rgba(0, 0, 0, 0.8) !important;
  transition: 0.4s;
  text-decoration: none;
  background-color: #f8f9fa !important;
}

.nav-link:hover {
  background-color: #f8f9fa !important;
}

.navbar-light .navbar-nav .active > .nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show > .nav-link {
  color: rgba(0, 0, 0, 0.8) !important;
}

.navbar-light .navbar-nav .show > .nav-link,
.navbar-light .navbar-nav .active > .nav-link,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .nav-link.active {
  color: rgba(0, 0, 0, 0.8) !important;
}

